import React from 'react';
import { sort_by } from '../utils/utilities';
import Header from '../common/header';
import "react-input-range/lib/css/index.css";
import Sort from '../common/sort';
import './../styles/_home.scss';
import Products from '../common/products';
import Filter from '../common/filter';
// import { products } from '../utils/dummy-data';

class HomeComponent extends React.Component {

    static calculateDiscount = (product, key) => {
        return product[key] - (product[key] * product.discount / 100);
    }

    state = {
        products: [],
        // products: products.products,
        filteredProducts: [],
        // filteredProducts: products.products,
        range: {
            min: 0,
            max: 0
        },
        value: {
            min: 0,
            max: 0
        },
        isFiltered: false,
        cartItemsCount: 0
    };

    componentDidMount() {
        this.fetchProducts();
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.productsData && (prevState.products !== nextProps.productsData) && !prevState.isFiltered) {
            return {
                range: {
                    min: Math.min.apply(Math, nextProps.productsData.map(function (o) { return Math.floor(o.price / 100) * 100; })),
                    max: Math.max.apply(Math, nextProps.productsData.map(function (o) { return Math.floor(o.price / 100) * 100; }))
                },
                value: {
                    min: Math.min.apply(Math, nextProps.productsData.map(function (o) { return Math.floor(o.price / 100) * 100; })),
                    max: Math.max.apply(Math, nextProps.productsData.map(function (o) { return Math.floor(o.price / 100) * 100; })),
                },
                products: nextProps.productsData.sort(sort_by('price', false, HomeComponent.calculateDiscount)),
                filteredProducts: nextProps.productsData.sort(sort_by('price', false, HomeComponent.calculateDiscount)),

            };
        }
        else if (nextProps.cart.cart.length !== prevState.cartItemsCount) {
            return {
                cartItemsCount: nextProps.cart ? nextProps.cart.cart.length : 0
            }
        }
        return null;
    }

    render() {
        const { filteredProducts, range, value, cartItemsCount } = this.state;
        const { requestAddToCartRequest } = this.props;
        const { priceRangeFilter, sortedProducts, changeRangeValue } = this;
        return (
            <>
                <Header cartItemsCount={cartItemsCount} />
                {/* <div>
                    <div>Search</div>
                    <input />
                </div> */}
                <section className="home">
                    <div className="home__wrapper">
                        <div className="home__wrapper__filter">
                            {filteredProducts && filteredProducts.length > 0 && <Filter range={range} value={value} changeRangeValue={changeRangeValue} priceRangeFilter={priceRangeFilter} />}
                        </div>
                        {filteredProducts && filteredProducts.length > 0 && <div className="f-c">
                            <Sort sortedProducts={sortedProducts} />
                            <Products
                                products={filteredProducts}
                                requestAddToCartRequest={requestAddToCartRequest}
                            />
                        </div>
                        }
                    </div>
                </section>
            </>
        );
    }



    fetchProducts = () => {
        const { requestProductsJSON } = this.props;
        requestProductsJSON();
    };

    sortedProducts = (key, isAsc) => {
        const { products } = this.state;
        this.setState({
            products: products.sort(sort_by(key, isAsc, HomeComponent.calculateDiscount))
        });
    }

    priceRangeFilter = () => {
        const { value, products } = this.state;
        this.setState({
            isFiltered: true,
            filteredProducts: products.filter(function (x) { return x.price >= value.min && x.price <= value.max })
        });
    };

    changeRangeValue = value => {
        this.setState({
            value
        });
    };
}

export default HomeComponent;